import React from "react";
import {Link} from "react-router-dom";
import Nav from "../components/nav";
import rest from '../images/rest.jpeg';

import '../index.css';
 
function LivingLife(props) {
  return (
    <div id="top">
      <Nav />

      <div className="BodyContent" id="opp">
        <h2>Living Life</h2>

        <p id="pp">Attending "UC Socially Dead" as a design student during the pandemic when college is supposed to be the “best time of your life” 
        is the epitome of cognitive dissonance. We want community and social interaction as people and as design students who can learn from each other. 
        We seek friends who can mutually cherish the fond memories of staying up late on a Figma file, smelling like smoke after a beach bonfire, sinking 
        into a food coma at Convoy, or consoling the fears of post-grad plans. We want to feel belonging, comfort, and empowerment from the company of 
        others.</p>
        <p id="pp">Connection can be only a few clicks away to a Zoom link that opens a GBM or lecture, but the lurking fatigue wrecks 
        your internal architecture. There are many days where it’s hard to get out of bed or focus on assignments. Serendipitous encounters with other 
        students have dwindled. It’s year two of the pandemic, and our bodies are keeping score. Amidst the stress, anxiety, and confusion, surviving the 
        day is enough.</p>
        <p id="pp">Sometimes, it feels like being human is our last priority. There’s guilt associated with choosing to indulge in hobbies, do nothing 
        over break, or hang out with friends instead of pulling our shit together to be proper adults. Happiness is delayed to the future and morphs 
        into a dangling incentive to continue grinding for that internship or job. This is reinforced when people tell us that hard work pays off. If 
        we can flip the script for our families and earn steady incomes as creatives, we’ve made it.</p>
        
        <p className="quote">“I thought that if I worked hard, I could achieve great things. I kept sleeping at 5am just grinding it out, 
        learning new tools, and making my portfolio. It wasn’t until I spent more time with my friends that I realized that there’s more to life than just  
        working.”</p>

        <img src={rest} alt="Pokemon - don't wanna be the very best" className="responsive2 Doodle" />

        <p id="pp">How can we design for people if we can’t nourish ourselves as individuals first? The non-design, non-career, non-academic aspects 
        of life that make us feel fulfilled - they don’t need to be neglected or sacrificed for the sake of swole salaries and societal expectations. 
        The time that’s invested in observing the world, making small and personal wins, or resting on weekends is an investment in our wellbeing.</p>
      </div>

      <div className="bottomNav">
        <div>
        <Link to="/Jobs">← Job Hunting</Link>
        </div>
        <div>
        <a href="#top">Scroll to Top</a>
        </div>

        <div>
        <Link to="/Reflection">Reflection →</Link>
        </div>
      </div>
    </div>
  );
}
 
export default LivingLife;